#cfg_lesson_order_modal {
  position: relative;
  z-index: 2000;

  .draggable-lesson-item:hover {
    background-color: #ddd;
  }
}

.draggable-lesson-item {
  cursor: pointer;
  background: #eee;
  position: relative;
  z-index: 2000;
  border-radius: 5px;
  .order {
    width: 50px;
    height: 50px;
    line-height: 50px;
    background-color: #03a9f4;
    border-radius: 25px;
    text-align: center;
    color: #fff;
    font-weight: bold;
    font-size: 15px;
  }
}
